@font-face {
    font-family: 'LINESeedKR-Bd';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_11-01@1.0/LINESeedKR-Rg.woff2') format('woff2');
    font-weight: 100;
    font-style: normal;
}

.header_main {
    width: 1200px;
    margin-left: auto;
    margin-right: auto;
    display: block;
}

.header_main_Logo > a > img {
    display: block;
    margin: auto;
}

.header_main_LogoAndInfo {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.header_main_LogoAndInfo > div {
    display: inline;
}

.header_main_ShopMenu > ul {
    margin-right: 10px;
    margin-top: 100px;
    padding-right: 30px;
    list-style: none;
}

.header_main_ShopMenu > ul > li {
    margin-left: 20px;
    display: inline;
}

Nav {
    margin-top: 20px;
    width: 1200px;
    list-style-type: none;
    text-align: center;
    padding: 0;
    justify-content: center;
    display: grid;
    grid-template-columns: repeat(5,1fr);
}

.NavLink:hover {
    color: #BAD7E9;
    background-color: #2B3467;
    transition-property: color,background-color;
    transition-duration: 330ms;

}

.NavLink {
    padding-top: 10px;
    padding-bottom: 10px;
    color:black;
    text-decoration: none;
    font-size:20px;
    font-family: 'LINESeedKR-Bd';
    text-align: center;
    display: block;
}